.Carousel {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 40px;

  &__activ {
    background-color: #ca0100;
    border-radius: 70%;
    width: 20px;
    height: 20px;
    display: flex;
    margin-right: 10px;
  }

  &__activ-element {
    background-color: #ffffff;
    border-radius: 70%;
    width: 10px;
    height: 10px;
    margin: auto;
  }

  &__no-activ {
    background-color: #ffffff;
    border-radius: 70%;
    width: 10px;
    height: 10px;
    margin-right: 10px;
  }
}
