.Promo {
  min-height: 600px;

  //background-image: url("../../../images/orig.webp");
  background-repeat: no-repeat;
    background-position: 100% 100%;
    background-size: cover;
  font-family: "Roboto";
  font-style: normal;
  width: 100%;
  border-radius: 25px;
  max-width: 1400px;
  margin-left: 20px;
  margin-right: 20px;
  //transition: 0.4s;
  display: flex;
  transition: all 1s ease-in-out;

  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-left: auto;
    margin-right: auto;
  }

  &__next {
    margin-left: 20px;
    margin-right: 20px;

    &_rotate {
      transform: rotate(180deg);
    }
  }

  &__slide {
    width: 100%;
    text-align: left;
    max-width: 1200px;
    padding-top: 40px;
  }

  &__slide-title {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 700;
    font-size: 60px;
    line-height: 70px;
    color: #ffffff;
  }

  &__slide-slick {
    color: #ffffff;
  }

  &__slide-phone {
    color: #ffffff;
    font-size: 24px;
    line-height: 28px;
  }

 &__page {
  width: 100%;
 }
}


@media (max-width: 768px) {
  .Promo {

    min-height: 360px;



    &__slide-title  {
      font-size: 36px;
line-height: 42px;
    }
  }
}



@media (max-width: 500px) {
  .Promo {
    &__slide-title  {
      font-size: 30px;
line-height: 35px;
    }
  }
}